<template>
  <div class="goods">
      <div class="menu-wrap" ref="wrapper">
         <ul>
            <li v-for='(item,i) in goods'  @click='selectMenu(i)' class="menu-item" :key="i" :class = "{'current' : i === currentIndex}">
                <span class="shop">{{item.name}}</span>
            </li>
          </ul>
      </div>
     <div class="foods-wrapper" ref="food">
      <ul>
        <li v-for='(item,i) in goods'  :key="i" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li  v-for="(food,r) in item.foods"  :key="r" class="food-item" >
              <div class="icon">
                <img :src="food.icon" @click='showFood(food)'>
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span>
                  <span class="count">好评{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cart">
								<cartControl :food = 'food'></cartControl>
							</div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <div>
    </div>
      <foodDetail></foodDetail>
      <!-- <shopcart :goods = 'goods' class="shop"></shopcart> -->
  </div>
</template>
 
<script>
import BScroll from "better-scroll";
import { mapGetters } from "vuex";
// import shopcart from "../shopcart/shopcart";
import foodDetail from '../foodDetail/foodDetail.vue';
import cartControl from '../cartControl/cartcontrol';

export default {
  props: ["name"],
  components: {
      // shopcart,
      foodDetail,
      cartControl
  },
  data(){
    return{
      listHeight:[],
      scrollY:0
    }
  },
  created() {
    this.$store.dispatch("getGoods");
  },
  watch: {
    goods() {
      this.$nextTick(() => {
        this.menuBS();
        this.calcHeight();
      });
    }
  },
  computed: {
    ...mapGetters(["goods"]),

    currentIndex(){
      for(let i = 0; i< this.listHeight.length; i++){
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i+1];
        if( this.scrollY >= height1 && this.scrollY < height2){
          return i;
        }
      }
      return 0;
    }
    
  },
  methods: {
    menuBS() {
      let menuBScroll = new BScroll(this.$refs.wrapper, {
        click:true
      });
      this.foodScroll = new BScroll(this.$refs.food, {
        click:true,
      });
      this.foodScroll.on('scroll',(pos) => {
        this.scrollY =  Math.abs( Math.round(pos.y))+2;
      })
    },
    calcHeight(){
      let foodList = this.$refs.food.getElementsByClassName('food-list');
      let height = 0;
      this.listHeight.push(height);
      for(let i = 0; i < foodList.length;i++){
        height +=foodList[i].clientHeight;
        this.listHeight.push(height);
      }
    },
    selectMenu(i){
				console.log(i);
				let foodList = this.$refs.food.getElementsByClassName('food-list-hook');
				let el = foodList[i];
				this.foodScroll.scrollToElement(el,300);
    },
    showFood(food){
				this.$store.dispatch('showFood',food);
		}
  }
}
</script>

<style lang="less" scoped>
  .goods {
    position: absolute;
    display: flex;
    overflow: hidden;
    width: 100%;
    top: 3.52rem;
    bottom: 0.9rem;
    .menu-wrap {
      flex: 0 0 1.6rem;
      width: 1.6rem;
      background: #f3f5f7;
      .menu-item {
        width: 1.12rem;
        height: 1.08rem;
        padding: 0 0.24rem;
        display: table;
        &.current{
          background: rgba(7,17,27,0.1);
          color: rgb(240, 20, 20);
				}
        .shop {
          display: table-cell;
          font-size: 0.24rem;
          font-weight: 200;
          text-align: center;
          line-height: 0.28rem;
          vertical-align: middle;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        }
      }
    }
    .foods-wrapper{
      flex: 1;
      .food-list{
        .title{
          padding-left:0.28rem;
          height: 0.52rem;
          line-height: 0.52rem;
          border-left: 0.02rem solid #d9dde1;
          font-size: 0.24rem;
          color: rgb(147, 153, 159);
          background: #f3f5f7;
        }    
      .food-item{
        display :flex;
        margin :0.36rem;
        padding-bottom :0.36rem;
        border-bottom:0.02rem solid rgba(7, 17, 27, 0.1);          
        .icon{
          flex: 0 0 57px;
          margin-right: 0.2rem;
          img{
            width: 1.14rem;
          }
        }          
        .content{
          flex :1;
          .name{
            margin :0.04rem 0 0.16rem 0;
            font-size: 0.28rem;
            line-height: 0.28rem;
            height :0.28rem;
            color: rgb(7, 17, 27);
          }            
          .desc, .extra{
            font-size :0.2rem;
            line-height: 0.2rem;
            color: rgb(147, 153, 159);
          }
          .desc{
            margin-bottom: 0.16rem;
            line-height: 0.24rem;
          }            
          .extra{
            .count{
              margin-right: 0.24rem;
            }    
          }            
          .price{
            margin-top: 0.16rem;
            font-weight: 700;
            line-height: 0.24rem;
            .now{
              margin-right: 0.16rem;
              font-size: 0.28rem;
              color: rgb(240, 20, 20);
            }
            .old{
              font-size :0.2rem;
              color: rgb(147, 153, 159);
              text-decoration: line-through;
            }
          } 
          .cart{
            position: absolute;
            right: 0;
          }
        }      
      }    
    }  
    .goods .foods-wrapper .food-list .food-item .content .cart[data-v-6a77ff90]{
      position: absolute;
      right: 0.5rem;
      padding: 0.4rem;
    }
  }

  }
</style>



