<template>
	<div class="ratings" >
        <div class="ratings-content">
            <div class="pf">
                <div class="left">
                    <h1 class="score">{{seller.score}}</h1>
                    <div class="title">综合评分</div>
                    <div class="rank">高于周边商家{{seller.rankRate}}%</div>
                </div>
                <div class="right">
                    <div class="sc">
                        <span class="title">服务态度</span>
                        <star :size="36" :score="seller.serviceScore"></star>
                        <span class="score">{{seller.serviceScore}}</span>
                    </div>
                    <div class="sc">
                        <span class="title">商品评分</span>
                        <star :size="36" :score="seller.foodScore"></star>
                        <span class="score">{{seller.foodScore}}</span>
                    </div>
                    <div class="delivery">
                        <span class="title">送达时间</span>
                        <span class="time">{{seller.deliveryTime}}分钟</span>
                    </div>
                </div>
            </div>
            <div class="fenge"></div>
            <ratingSelect  :ratings="ratings"></ratingSelect>
            <div class="rating-wrapper" ref="food">
                <ul>
                    <li v-for='item in selectRatings' class="rating-item" >
                        <div class="avatar">
                            <img :src="item.avatar" alt="" width="28" height="28">
                        </div>
                        <div class="content">
                            <h1 class="name">{{item.username}}</h1>
                            <div class="star-wrapper">
                                <star :size="24" :score="item.score"></star>
                                <span class="delivery" v-show="item.deliveryTime">
                                    {{item.deliveryTime}}
                                </span>
                            </div>
                            <p class="text">{{item.text}}</p>
                            <div class="recommend" v-show="item.recommend &&item.recommend.length">
                                <i class="iconfont icon-damuzhion"></i>
                                <span  class="item" v-for="item in item.recommend" >{{item}}</span>
                            </div>
                            <div class="time">
                                {{item.rateTime}}
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
		</div>
	</div>	
</template>

<script>
	import BScroll from "better-scroll";
	import { mapGetters } from 'vuex';
    import star from "../star/star.vue";
    import ratingSelect from '../ratingSelect/ratingSelect.vue';
    export default {
        created(){
            this.$store.dispatch('getRatings');
        },
        watch: {
			selectRatings() {
				this.$nextTick(() => {
					this.menuBS()
				});
			}
		},
        components: {
            star,
            ratingSelect
        },
        computed:{
           ...mapGetters([
               'seller',
               'selectRatings',
               'ratings',
				'aa'
           ]),
       },
        methods:{
            menuBS() {
				let menuBScroll = new BScroll(this.$refs.food, {
					click:true
				})
			}
        },
	}
</script>

<style lang="less" scoped>
    .ratings{
        width: 100%;
        overflow: hidden;
        .pf{
            display: flex;
            padding: 0.36rem 0 0.48rem 0;
            .left{
                flex: 0 0 2.75rem;
                padding: 0.12rem 0;
                border-right: 1px solid rgba(1, 17, 27, 0.1);
                text-align: center;
                .score{
                    margin-bottom: 0.12rem;
                    line-height: 0.56rem;
                    font-size: 0.48rem;
                    color: rgb(255, 153, 0);
                }
                .title{
                    margin-bottom: 0.16rem;
                    line-height: 0.24rem;
                    font-size: 0.24rem;
                    color: rgb(7, 17, 27);
                }
                .rank{
                    line-height: 0.24rem;
                    font-size: 0.2rem;
                    color: rgb(147,153,159); 
                }
            }
            .right{
                flex: 1;
                padding: 0.12rem 0 0.12rem 0.48rem;
                .sc{
                    line-height: 0.36rem;
                    margin-bottom: 0.16rem;
                    font-size: 0.24rem;
                    .title{
                        display: inline-block;
                        vertical-align: top;
                        line-height: 0.36rem;
                        color: rgb(7, 17, 27);
                    }
                    .star{
                        display: inline-block;
                        margin: 0 0 0 0.22rem;
                        vertical-align: top;                        
                    }
                    .score{
                        display: inline-block;
                        vertical-align: top;                        
                        line-height: 0.36rem;
                        font-size: 0.24rem;
                        color: rgb(255, 153, 0);
                        margin-left: 0.08rem;
                        margin-right: 0.48rem;                       
                    }
                }
                .delivery{
                    font-size: 0.24rem;
                    margin-top: 0.16rem;
                    .title{
                        display: inline-block;
                        vertical-align: top;
                        line-height: 0.36rem;
                        color: rgb(7, 17, 27);
                    }
                    .time{
                        display: inline-block;
                        vertical-align: top;                        
                        line-height: 0.36rem;
                        font-size: 0.24rem;
                        color: rgb(147, 153, 159);
                        margin-left: 0.48rem;   
                    }
                }
            }
        } 
        .fenge{
			width: 100%;
			height: 0.32rem;
			background: #f3f5f7;
			border-top: 1px solid rgba(7,17,27,0.1);
			border-bottom: 1px solid rgba(7,17,27,0.1);
        }
        .rating-wrapper{
            height: 5rem;
            overflow: hidden;
            padding: 0 0.36rem;
            .rating-item{
                display: flex;
                padding: 0.36rem 0;
                border-bottom: 0.02rem solid  rgba(1, 17, 27, 0.1);
                .avatar{
                    flex: 0 0 0.56rem;
                    width: 0.56rem;
                    margin-right: 0.24rem;
                    img{
                        border-radius: 50%;
                    }
                } 
                .content{
                    position: relative;
                    flex: 1;
                    .name{
                        margin-bottom: 0.08rem;
                        line-height: 0.24rem;
                        font-weight: 700;
                        font-size: 0.2rem;
                        color: rgb(7, 17, 27);
                    }
                    .star-wrapper{
                        margin-bottom: 0.12rem;
                        font-size: 0;
                        .star{
                            display: inline-block;
                            margin-right: 0.32rem;
                            vertical-align: top;
                        }
                        .delivery{
                            display: inline-block;
                            vertical-align: top;
                            font-size: 0.2rem;
                            line-height: 0.24rem;
                            color: rgb(147, 153, 159);
                        }

                    }
                    .text{
                        line-height: 0.36rem;
                        color: rgb(7, 17, 27);
                        font-size: 0.24rem;
                        margin-bottom: 0.16rem;
                    }
                    .recommend{
                        line-height: 0.32rem;
                        font-size: 0;
                        .iconfont, .item{
                            display: inline-block;
                            margin: 0 0.16rem 0.08rem 0;
                            font-size: 0.18rem;
                        }
                        .iconfont{
                            color: rgb(0, 160, 220);
                        }
                        .item{
                            padding: 0 0.12rem;
                            border: 0.01rem solid rgba(7, 17, 27, 0.1);
                            border-radius: 0.02rem;
                            color: rgb(147, 153, 159);
                            background: #ffffff;
                        }
                    }
                    .time{
                        position: absolute;
                        top: 0;
                        right: 0;
                        line-height: 0.24rem;
                        font-size: 0.2rem;
                        color: rgb(147, 153, 159);
                    }
                }
            }
        }
    }






	
</style>


