<template>
  <div class="seller"  ref="food">
    <div class="conent">
        <div class="top">
            <div class="sn">
            <h1>{{seller.name}}</h1>
            </div>
            <div class="desc">
                <star :size="36" :score="seller.score"></star>
                <span class="text">({{seller.ratingCount}})</span>
                <span class="text">月售{{seller.sellCount}}单</span>
            </div>
            <ul class="mark">
                <li class="block">
                    <h2>起送价</h2>
                    <div class="text">
                        <span class="money">{{seller.minPrice}}</span>元
                    </div>
                </li>
                <li class="block">
                    <h2>商家配送</h2>
                    <div class="text">
                        <span class="money">{{seller.deliveryPrice}}</span>元
                    </div>
                </li>
                <li class="block">
                    <h2>平均配送时间</h2>
                    <div class="text">
                        <span class="money">{{seller.deliveryTime}}</span>分钟
                    </div>
                </li>
            </ul>
            <div class="favorite">
                <i class="iconfont icon-xin" :class="{'active':favorite}" @click="favorite=!favorite"></i>
                <p>{{favoriteText}}</p>
            </div>                    
        </div>
        <div class="fenge"></div>
        <div class="active">
            <h1 class="title">公告与活动</h1>
            <div class="content">
                <p class="text">{{seller.bulletin}}</p>                
            </div>
            <div class="info">
                <div class="jian">
                    <img src="../../../static/imgs/decrease.png" alt="">
                    <p v-if='seller.supports'>{{seller.supports[0].description}}</p>
                </div>
                <div class="zhed">
                    <img src="../../../static/imgs/discount.png" alt="">
                    <p v-if='seller.supports'>{{seller.supports[1].description}}</p>
                </div>
                <div class="zheq">
                    <img src="../../../static/imgs/discount.png" alt="">
                    <p v-if='seller.supports'>{{seller.supports[2].description}}</p>
                </div>
                <div class="te">
                    <img src="../../../static/imgs/special.png" alt="">
                    <p v-if='seller.supports'>{{seller.supports[3].description}}</p>
                </div>
                <div class="ted">
                    <img src="../../../static/imgs/guarantee.png" alt="">
                    <p v-if='seller.supports'>{{seller.supports[4].description}}</p>
                </div>
            </div>
        </div>
        <div class="fenge"></div>
        <div class="pics">
            <h1 class="title">商家实景</h1>
            <div class="pic-wrapper" ref="menuWrap">
                <ul class="pic-list" ref="picList">
                    <li class="pic-item" v-for="item in seller.pics">
                        <img :src="item">
                    </li>
                </ul>
            </div>
        </div>
        <div class="fenge"></div>
        <div class="info">
            <div class="title  border-1px">商家信息</div>
            <ul>
                <li class="info-item" v-for="info in seller.infos">
                    <span class="text">	{{info}}</span>
                </li>
            </ul>
        </div>
    </div>
    </div>
  </div>
  <!-- <shopcart :goods = 'goods' class="shop"></shopcart> -->
  </div>
  
</template>

<script>
    import { mapGetters } from 'vuex';
    import star from '../star/star.vue';
    import BScroll from "better-scroll";
    import shopcart from "../shopcart/shopcart";

    export default {
        components:{
            star,
            shopcart
        },
        data(){
            return{
                favorite:false
            }
        },
        watch: {
			seller() {
				this.$nextTick(() => {
					this.menuBS()
                });
                let picWidth = 120;
	          	let margin = 6;
	          	let width = (picWidth + margin) * this.seller.pics.length - margin;
	          	this.$refs.picList.style.width = width + 'px';

 				if( !this.picBScroll ){
	 				this.picBScroll = new BScroll( this.$refs.menuWrap , {
	 					scrollX: true,
	 				})
	 			}else{
	 				this.picBScroll.refresh();
	 			}
            },
            
        },
        computed: {
            ...mapGetters(["seller"]),
            favoriteText() {
                return this.favorite ? "已收藏" : "收藏";
            }
        },
        methods: {
             menuBS() {
				let menuBScroll = new BScroll(this.$refs.food, {
					click:true
				})
			},
        }
    }
</script>

<style lang="less" scoped>
    .seller{
        position: relative;
        top: 0;
        bottom: 0.8rem;
        left: 0;
        width: 100%;
        overflow: hidden;
        .top{
            padding: 0.36rem;
            .sn{
                margin-bottom: 0.16rem;            
                line-height: 0.28rem;
                color: rgb(7, 17, 27);
                font-size: 0.28rem;
            }
            .desc{
                padding-bottom: 0.36rem;
                font-size: 0;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                .star{
                    display: inline-block;
                    vertical-align: top;
                    margin-right: 0.16rem;
                } 
                .text{
                    display: inline-block;
                    vertical-align: top;
                    margin-right: 0.24rem;
                    line-height: 0.36rem;
                    font-size: 0.2rem;
                    color: rgb(77, 85, 93);
                }
            }
            .mark{
                 display: flex;
                 padding-top:0.36rem;
                .block{
                    flex: 1;
                    text-align: center;
                    border-right: 1px solid rgba(7, 17, 27, 0.1);
                    &:last-child{
                        border: none;
                    }
                    h2{
                        margin-bottom: 0.08rem;
                        line-height: 0.2rem;
                        font-size: 0.2rem;
                        color: rgb(147, 153, 149);
                    }
                    .text{
                        line-height: 0.48rem;
                        font-size: 0.2rem;
                        color: rgb(7, 17, 27);
                        .money{
                            font-size: 0.48rem;
                        }
                    }
                    
                }

            }
            .favorite{
                position: absolute;
                right: 0.22rem;
                top: 0.36rem;
                width: 1rem;
                text-align: center;
                .iconfont{
                    margin-bottom: 0.08rem;
                    line-height: 0.48rem;
                    font-size: 0.48rem;
                    width: 1rem;
                    color: #d4d6d9;
                    &.active{
                        color:rgb(240,20,20);
                    }
                }
                .text{
                    text-align: center;
                    line-height: 0.2rem;
                    font-size: 0.2rem;
                    color:rgb(77,85,93);
                }
            }
            
        }
        .fenge{
			width: 100%;
			height: 0.32rem;
			background: #f3f5f7;
			border-top: 0.02rem solid rgba(7,17,27,0.1);
			border-bottom: 1px solid rgba(7,17,27,0.1);
        }
        .active{
            padding: 0.36rem 0.24rem 0 0.36rem;
            .title {
                margin-bottom:0.16rem;
                line-height: 0.28rem;
                color: rgb(7, 17, 27);
                font-size:0.28rem;
            }
            .content{
                padding: 0 0.24rem 0.32rem 0.02rem;
                border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
                .text{
                   line-height: 0.48rem;
                    font-size: 0.24rem;
                    color: rgb(240, 20, 20);
                    margin-left: 0.24rem;
                }
            }
        
            .info {
                padding-top:0 ;
                color: rgb(7, 17, 27);
                font-size: 0.24rem;
                font-weight: 200;
                line-height: 0.24rem;  
                div{
                    width: 100%;
                    border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
                    // margin-bottom: 0.32rem;
                }
                img {
                    width: 0.38rem;
                    padding: 0.32rem 0.12rem 0.32rem 0;
                }
                .jian p {
                    position: absolute;
                    top: 7.38rem;
                    left: 1.55rem;
                }
                .zhed p {
                    position: absolute;
                    top: 8.48rem;
                    left: 1.55rem;
                }
                .zheq p {
                    position: absolute;
                    top: 9.52rem;
                    left: 1.55rem;
                }
                .te p {
                    position: absolute;
                    top: 10.6rem;
                    left: 1.55rem;
                }
                .ted p {
                    position: absolute;
                    top: 11.66rem;
                    left: 1.55rem;
                }
            }

        }
        .pics {
            padding: 0.36rem;
            .title {
                margin-bottom: 12px;
                line-height: 14px;
                color: rgb(7, 17, 27);
                font-size: 14px;
            }
            .pic-wrapper {
                width: 100%;
                overflow: hidden;
                white-space: nowrap;
                .pic-list {
                    font-size: 0;
                    .pic-item {
                        display: inline-block;
                        margin-right: 0.12rem;
                        width: 2.4rem;
                        height: 1.8rem;
                        img{
                            width: 100%;
                            height: 100%;
                        }
                        &:last-child {
                            margin: 0.1rem;
                        }
                    }
                }
            }
        }
        .info {
            padding:0.36rem 0.36rem 0 0.36rem;
            color: rgb(7, 17, 27);
            .title {
                padding-bottom: 0.24rem;
                line-height: 0.28rem;
                border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
                font-size: 0.28rem;
            }
            .info-item {
                padding: 0.32rem 0.24rem;
                line-height: 0.32rem;
                border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
                &:last-child{
                    border: none;
                }
                .text {
                    margin-left: 0.24rem;
                    font-size: 0.24rem;
                    font-weight: 200;
                }
            }
        }
        
    }

  
</style>



