<template>
	<div class="select">
        <div class="tips">
            <div class="all"  @click='select(2)' :class="{'active': selectType === 2}">{{types[2]}}{{ratings.length}}</div>
            <div class="rec"  @click='select(0)' :class="{'active': selectType === 0}">{{types[0]}}{{goodCount}}</div>
            <div class="disclose"  @click='select(1)'  :class="{'active': selectType === 1}">{{types[1]}}{{badCount}}</div>
        </div>
        <div class="toggle" @click = 'toggle' >
            <i class="iconfont icon-duihao" :class="{'active': onlyContent}"></i>
            <span class="text">只看有内容的评价</span>
        </div>
       
	</div>
</template> 

<script>
	import { mapGetters } from 'vuex';
	export default {
		data(){
			return {
				selectType:3
			}

		},
		computed:{
			...mapGetters([
				'types',
				'ratings',
				'selectRatings',
				'onlyContent'
			]),
			goodCount(){
				return this.ratings.filter( rating => {
					return rating.rateType === 0; 
				}).length;
			},
			badCount(){
				return this.ratings.filter( rating => {
					return rating.rateType === 1; 
				}).length;
			}
		},
		// 初始化页面
		created(){
			this.$store.dispatch('changeTypes',['推荐','吐槽','全部']);
		},
		methods:{
			select(c){
				this.selectType=c;
				this.$store.dispatch('select',c)
			},
			toggle(){
				this.$store.dispatch('toggle');
			}
		}

	}
</script>

<style lang="less" scoped>
	.select{
			padding-left:0.36rem ;
			.tips{
				width: 6.68rem;
				padding: 0.36rem 0.16rem 0.36rem 0;
				border-bottom: 1px solid rgba(7,17,27,0.1);
				display: flex;
				flex: 1;
				font-size: 0.24rem;
				.all{
					width: 1.18rem;
					height: 0.62rem;
					border-radius: 0.02rem;
					background: #00a0dc;
					margin-right: 0.16rem;
					text-align: center;
					color: rgb(78,85,93);
					line-height: 0.62rem;
					&.active{
                         color:rgb(255,255,255);
                    }
				}
				.rec{
					width: 1.18rem;
					height: 0.62rem;
					border-radius: 0.02rem;
					background: #ccecf8;
					margin-right: 0.16rem;
					text-align: center;
					color: rgb(78,85,93);
					line-height: 0.62rem;
					&.active{
                         color:rgb(255,255,255);
                    }
				}
				.disclose{
					width: 1.18rem;
					height: 0.62rem;
					border-radius: 0.02rem;
					background: #e9ebec;
					margin-right: 0.16rem;
					text-align: center;
					color: rgb(78,85,93);
					line-height: 0.62rem;
					&.active{
                         color:rgb(255,255,255);
                    }
				}
			}
		}
		.toggle{
			padding: 0.24rem 0 0.24rem 0.36rem;
			line-height: 0.48rem;
			font-size: 0.48rem;
			color:rgb(147,153,159);
			border-bottom: 1px solid rgba(1, 17, 27, 0.1);
			.iconfont{
				vertical-align: top;
				font-size: 0.24rem;
				margin-right: 0.08rem;
				&.active{
					color:rgb(0, 160, 220);
				}
			}
			.text {
				font-size: 0.24rem;
			}

		}
</style>


