import types from '../types.js'
import axios from 'axios'

var state = {
    seller : {},
    detail:false
}

var getters = {
    seller(state){
        return state.seller;
    },
    detail(state){
        return state.detail
    }
}

var actions = {
    getSeller({commit,state}){
       axios.get('./data.json').then((res)=>{
            commit( types.GET_SELLER,res.data.seller);
        });
    // axios.get('/api/seller').then((res)=>{
    //     if(res.data.errno === 0){
    //         commit( types.GET_SELLER,res.data.data);
    //      }
    //  });
    },
    showDetail({commit}){
        commit(types.showDetail);
    },
    hideDetail({commit}){
        commit(types.hideDetail)

    }
}

var mutations = {
    [types.GET_SELLER](state,data){
        state.seller = data;
    },
    [types.showDetail](state){
        state.detail = true;
    },
    [types.hideDetail](state){
        state.detail = false;
    }
}



export default{
    state,
    getters,
    actions,
    mutations
}


