<template>
  <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight"> -->
  <div class="detail" v-show="detail">
      <p  class="shop">{{seller.name}}</p>
      <div class="stars">
          <star :score = 'seller.score' :size = "'big'"></star>
      </div>
      <div class="xian"></div>
      <p class="yhui">优惠信息</p>
      <div class="xian2"></div>
      <div class="info">
        <div class="jian">
            <img src="../../../static/imgs/decrease.png" alt="">
            <p v-if='seller.supports'>{{seller.supports[0].description}}</p>
        </div>
        <div class="zhed">
            <img src="../../../static/imgs/discount.png" alt="">
            <p v-if='seller.supports'>{{seller.supports[1].description}}</p>
        </div>
        <div class="zheq">
            <img src="../../../static/imgs/discount.png" alt="">
            <p v-if='seller.supports'>{{seller.supports[2].description}}</p>
        </div>
        <div class="te">
            <img src="../../../static/imgs/special.png" alt="">
            <p v-if='seller.supports'>{{seller.supports[3].description}}</p>
        </div>
        <div class="ted">
            <img src="../../../static/imgs/guarantee.png" alt="">
            <p v-if='seller.supports'>{{seller.supports[4].description}}</p>
        </div>
      </div>
      <div class="xian3"></div>
      <p class="tip">商家公告</p>
      <div class="xian4"></div>
      <div class="bulletin">
          {{seller.bulletin}}
      </div>
      <div>
          <i class="iconfont icon-X" @click="hideDetail"></i>
      </div>

      
  </div>
  </transition>
</template>

<script>
import { mapGetters } from "vuex";
import animate from "animate.css";
import star from "../star/star.vue";
export default {
  props: ["name"],
  components: {
    star
  },
  computed: {
    ...mapGetters(["seller", "detail"])
  },
  methods: {
    hideDetail() {
      this.$store.dispatch("hideDetail");
    }
  }
};
</script>

<style lang="less" scoped>
.detail {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(7, 17, 27, 0.8);
  // filter: blur(0.1rem);
  .shop {
    font-size: 0.32rem;
    font-weight: 700;
    line-height: 0.32rem;
    padding-top: 1.28rem;
    text-align: center;
    color: rgb(255, 255, 255);
  }
  .stars {
    width: 100%;
    height: 0.48rem;
    text-align: center;
    padding: 0.32rem 0 0.56rem 0;
  }
  .xian {
    width: 2.24rem;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    margin-left: 0.72rem;
    padding-bottom: 0.48rem;
  }
  .xian2 {
    width: 2.24rem;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    margin-right: 0.72rem;
    position: absolute;
    top: 2.95rem;
    right: 0.01rem;
  }
  .yhui {
    color: rgb(255, 255, 255);
    font-size: 0.28rem;
    font-weight: 700;
    line-height: 0.28rem;    
    padding: 0.56rem 0.3rem 0.48rem 3.15rem;
    position: absolute;
    top: 2.25rem;
  }
  .info {
    color: rgb(255, 255, 255);
    font-size: 0.24rem;
    font-weight: 200;
    line-height: 0.24rem;
    img {
      width: 0.38rem;
      padding: 0.24rem 0.3rem 0rem 0.96rem;
    }
    .jian p {
      position: absolute;
      top: 3.75rem;
      left: 1.55rem;
    }
    .zhed p {
      position: absolute;
      top: 4.41rem;
      left: 1.55rem;
    }
    .zheq p {
      position: absolute;
      top: 5.05rem;
      left: 1.55rem;
    }
    .te p {
      position: absolute;
      top: 5.75rem;
      left: 1.55rem;
    }
    .ted p {
      position: absolute;
      top: 6.35rem;
      left: 1.55rem;
    }
  }
   .xian3 {
    width: 2.24rem;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    margin-left: 0.72rem;
    padding-top: 0.56rem;
    position: absolute;
    top: 7.30rem;
    // left: 0.01rem;
  }
  .xian4 {
    width: 2.24rem;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    margin-right: 0.72rem;
    position: absolute;
    top:7.30rem;
    right: 0.01rem;
  }
  .tip {
    color: rgb(255, 255, 255);
    font-size: 0.28rem;
    font-weight: 700;
    line-height: 0.28rem;
    padding: 0.56rem 0.3rem 0.48rem 3.15rem;
    position: absolute;
    top: 6.6rem;
  }
  .bulletin{
    //   width: 5.57rem;
      color: rgb(255, 255, 255);
      font-size: 0.24rem;
      font-weight: 200;
      line-height: 0.48rem;
      padding: 1.04rem 0.96rem 0.48rem 0.96rem;
  }
  i{
     width: 0.64rem;
     height: 0.64rem;
     font-size: 0.64rem;
     color: rgba(255, 255, 255, 0.5);
     position: absolute;
     bottom: 0.64rem;
     left: 3.4rem;
  }
}
</style>



