import types from '../types.js'
import axios from 'axios'

var state = {
    goods:[],
    food:{},
    showfood:false
}

var getters = {
    goods(state){
        return state.goods;
    },
    food(state){
        return state.food;
    },
    showfood(state){
        return state.showfood;
    }
}

var actions = {
    getGoods({commit,state}){
        axios.get('./data.json').then((res)=>{
            commit(types.GET_GOODS,res.data.goods);
        });
        // axios.get('/api/goods').then((res)=>{
        //     if(res.data.errno === 0 ){
        //         console.log(res);
        //         commit(types.GET_GOODS,res.data.data);
        //     }
        // });
    },
    showFood({commit,state},f){
        commit('showffood',f)
    },
    closeDetail({commit}){
		commit('closeDetail');
	}
    
}

var mutations = {
    [types.GET_GOODS](state,data){
        state.goods = data;
    },
    showffood(state,f){
        state.food=f;
        state.showfood=true;
    },
    closeDetail(state){
		state.showfood = false;
	}
    
    
}



export default{
    state,
    getters,
    actions,
    mutations
}


