<template>
	<div class="food" v-show='showfood' ref="food">
		<div>
			<div class="imgshow"  >
				<img :src="food.image" alt="" @click="closeDetail">
				<div class="content">
                	<h2 class="name">{{food.name}}</h2>
                	<div class="extra">
                  		<span class="count">月售{{food.sellCount}}份</span>
                  		<span class="count">好评{{food.rating}}%</span>
                	</div>
                	<div class="price">
                  		<span class="now">￥{{food.price}}</span>
                  		<span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                	</div>  
					<div class="shopcart">
						<p class="jion" v-show='!food.count' @click = 'addFirst'>加入购物车</p>
						<cartControl :food = 'food' v-show='food.count'></cartControl>
					</div>      
            	</div>
			</div>
			<div class="fenge"></div>
			<div class="introduce">
				<div class="int">商品介绍</div>
				<div class="info">{{food.info}}</div>
			</div>
			<div class="fenge1"></div>
			<div class="assess">
				<div class="title">商品评价</div>
				<ratingSelect  :ratings="ratings"></ratingSelect>
				<div class="rating-wrapper" ref="wrapper">
					<ul>
						<li v-for='item in selectRatings' class="rating-item" >
							<div class="avatar">
								<img :src="item.avatar" alt="" width="28" height="28">
							</div>
							<div class="content">
								<h1 class="name">{{item.username}}</h1>
								<div class="star-wrapper">
									<star :size="24" :score="item.score"></star>
									<span class="delivery" v-show="item.deliveryTime">
										{{item.deliveryTime}}
									</span>
								</div>
								<p class="text">{{item.text}}</p>
								<div class="recommend" v-show="item.recommend &&item.recommend.length">
									<i class="iconfont icon-damuzhion"></i>
									<span  class="item" v-for="item in item.recommend" >{{item}}</span>
								</div>
								<div class="time">
									{{item.rateTime}}
								</div>
							</div>
						</li>
					</ul>
            	</div>
			</div>
		</div>
	</div>	
</template>

<script>
	import BScroll from "better-scroll";
	import { mapGetters } from 'vuex';
	import Vue from 'vue';
	import cartControl from '../cartControl/cartcontrol.vue';
	import ratingSelect from '../ratingSelect/ratingSelect.vue';
	import star from "../star/star.vue";

	export default {
		created(){
            this.$store.dispatch('getRatings');
        },
		computed:{
			...mapGetters([
				'showfood',
				'food',
				'seller',
                'selectRatings',
                'ratings',
				'aa'
			])
		},
		components:{
			cartControl,
			star,
            ratingSelect
		},
		watch: {
			food() {
				this.$nextTick(() => {
					this.menuBS()
				});
			}
		},
		methods: {
			menuBS() {
				let menuBScroll = new BScroll(this.$refs.food, {
					click:true
				})
			},
			closeDetail(){
				this.$store.dispatch('closeDetail');
			},
			addFirst(){
				if( !this.food.count ){
					Vue.set( this.food , 'count' , 1 );
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.food {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: white;
		.imgshow{
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 100%;
			img{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
			 .content{
                flex :1;
          		.name{
            		padding :0.36rem 0 0.16rem 0.36rem;
					font-size: 0.28rem;
					font-weight: 700;
            		line-height: 0.28rem;
            		height :0.28rem;
            		color: rgb(7, 17, 27);
         		}            
          		.desc, .extra{
					padding-left: 0.36rem;
            		font-size :0.2rem;
            		line-height: 0.2rem;
            		color: rgb(147, 153, 159);
          			}
          		.desc{
					padding:0.16rem 0 0.24rem 0.36rem;
           			margin-top: 0.16rem;
            		line-height: 0.24rem;
          			}            
          		.extra{
            		.count{
              			margin-right: 0.24rem;
            		}    
          		}            
          		.price{
					margin-top: 0.36rem;
					padding-left: 0.36rem;
            		font-weight: 700;
            		line-height: 0.24rem;
            		.now{
              			margin-right: 0.16rem;
              			font-size: 0.28rem;
              			color: rgb(240, 20, 20);
            		}
            		.old{
              			font-size :0.2rem;
              			color: rgb(147, 153, 159);
              			text-decoration: line-through;
            		}
				}
				.shopcart{
					position: absolute;
					top: 8.65rem;
					left: 5.65rem;
					.jion{
						width: 1.48rem;
						height: 0.36rem;
						border-radius: 0.22rem;
						background:#00a0dc;
						text-align: center;
						font-size: .2rem;
						color: rgb(255,255,255);
						line-height: 0.36rem;
						padding: 0.12rem 0;
					}
				} 
			}
		}
		.fenge{
			width: 100%;
			height: 0.32rem;
			background: #f3f5f7;
			border-top: 1px solid rgba(7,17,27,0.1);
			border-bottom: 1px solid rgba(7,17,27,0.1);
			margin-top:2.05rem;
		}
		.introduce{
			width: 100%;
			.int{
				font-size: 0.28rem;
				padding: 0.36rem 0 0.12rem 0.36rem;
			}
			.info{
				padding: 0.12rem 0.52rem 0.36rem 0.52rem;
				font-size: 0.24rem;
				font-weight: 200;
				color: rgb(77,85,93);
				line-height: 0.48rem;
			}
		}
		.fenge1{
			width: 100%;
			height: 0.32rem;
			background: #f3f5f7;
			border-top: 1px solid rgba(7,17,27,0.1);
			border-bottom: 1px solid rgba(7,17,27,0.1);
		}
		.assess{
			padding-left:0.36rem ;
			.title{
				font-size: 0.28rem;
				padding: 0.36rem 0 0.12rem 0;
			}
			.rating-wrapper{
            padding: 0 0.36rem;
            .rating-item{
                display: flex;
                padding: 0.36rem 0;
                border-bottom: 0.02rem solid  rgba(1, 17, 27, 0.1);
                .avatar{
                    flex: 0 0 0.56rem;
                    width: 0.56rem;
                    margin-right: 0.24rem;
                    img{
                        border-radius: 50%;
                    }
                } 
                .content{
                    position: relative;
                    flex: 1;
                    .name{
                        margin-bottom: 0.08rem;
                        line-height: 0.24rem;
                        font-weight: 700;
                        font-size: 0.2rem;
                        color: rgb(7, 17, 27);
                    }
                    .star-wrapper{
                        margin-bottom: 0.12rem;
                        font-size: 0;
                        .star{
                            display: inline-block;
                            margin-right: 0.32rem;
                            vertical-align: top;
                        }
                        .delivery{
                            display: inline-block;
                            vertical-align: top;
                            font-size: 0.2rem;
                            line-height: 0.24rem;
                            color: rgb(147, 153, 159);
                        }

                    }
                    .text{
                        line-height: 0.36rem;
                        color: rgb(7, 17, 27);
                        font-size: 0.24rem;
                        margin-bottom: 0.16rem;
                    }
                    .recommend{
                        line-height: 0.32rem;
                        font-size: 0;
                        .iconfont, .item{
                            display: inline-block;
                            margin: 0 0.16rem 0.08rem 0;
                            font-size: 0.18rem;
                        }
                        .iconfont{
                            color: rgb(0, 160, 220);
                        }
                        .item{
                            padding: 0 0.12rem;
                            border: 0.01rem solid rgba(7, 17, 27, 0.1);
                            border-radius: 0.02rem;
                            color: rgb(147, 153, 159);
                            background: #ffffff;
                        }
                    }
                    .time{
                        position: absolute;
                        top: 0;
                        right: 0;
                        line-height: 0.24rem;
                        font-size: 0.2rem;
                        color: rgb(147, 153, 159);
                    }
                }
            }
		}
		}
	}
</style>


