<template>
  <div class="header" @click="showDetail">
      <div class="bgImg">
          <img :src="seller.avatar" alt="">
      </div>
      <div class="bg-gray"></div>
      <div class="avatar">
          <img :src="seller.avatar">
      </div>
      <div class="shop">
          <img src="../../../static/imgs/brand.png">
          <p class="dm">{{seller.name}}</p>
          <p class="fn">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
          <div class="desc">
              <img src="../../../static/imgs/decrease.png" alt="">
              <p v-if='seller.supports'>{{seller.supports[0].description}}</p>
          </div>
      </div>
      <div class="tz">
          <div class="hezi"></div>
          <p class="five">5个</p>
          <p class="kz1">></p>
      </div>
      <div class="sbg">
      </div>
      <div class="smbg">
          <img src="../../../static/imgs/bulletin.png" alt="">
          <p class="word">{{seller.bulletin}}</p>
      </div>
       <p class="kz">></p>
    <div class="clearfix"></div>
  </div>
</template>

<script>
//    import detail from '../detail/detail.vue'
   import { mapGetters , mapActions } from 'vuex';
   export default{
       computed:{
           ...mapGetters([
               'seller'
           ]),
       },
      methods:{
          ...mapActions([
              'showDetail'
          ])
       }
   }
</script>

<style lang="less" scoped>
    .header {
        width: 100%;
        height: 2.68rem;
        position: relative;
        .bgImg{
            z-index:-1;
            img{
            height: 2.68rem;
            width: 100%;
            filter: blur(0.1rem);
            }
           
        }
        .bg-gray,.avatar img,.shop .dm,.shop img,.shop .fn,.desc img,.desc p,.smbg,.smbg img,.smbg .word{
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
        }
        .bg-gray{
            height: 2.68rem;
            width: 100%;
            background: rgba(7, 17, 27,0.5);
        }
        .avatar img{
            width: 1.28rem;
            height: 1.28rem;
            padding: .48rem 0.32rem 0.36rem 0.48rem;
        }
        .shop ,.smbg,.hezi,.kz{
            color: rgb(255, 255, 255);
        }
        .shop img{
            height: 0.32rem;
            padding: .48rem 0.12rem 0.16rem 2.08rem;
        }
        .shop .dm{  
            padding: .48rem 0.12rem 0.12rem 2.8rem;
            font-size: 0.32rem;
            font-weight: bold;
            line-height: 0.36rem;
        }
        .shop .fn{
            padding: .96rem 0.12rem 0.12rem 2.08rem;
            font-size: 0.24rem;
            font-weight: 200;
            line-height: 0.24rem;
        }
        .desc img{
            padding: 1.4rem 0.12rem 0.04rem 2.08rem;
        }
        .desc p{
            padding: 1.44rem 0.12rem 0.04rem 2.44rem;
            font-size: 0.2rem;
            font-weight: 200;
            line-height: 0.24rem;
        }
        .tz{
            color: rgb(255, 255, 255);
            position: absolute;
            top: 1.26rem;
            right: 0.24rem;
            .hezi{
                width: 0.88rem;
                height: 0.48rem;
                background-color: rgba(0, 0, 0, 0.2);
                border: 0.01px solid rgba(0, 0, 0, 0.2);
                border-radius: 0.18rem;
            }
            .five{
                font-size: 0.2rem;
                font-weight: 200;
                line-height: 0.24px;
                position: absolute;
                top: 0.26rem;
                right: 0.35rem;
            }
            .kz1{
            font-size: 0.24rem;
            position: absolute;
            top: 0.12rem;
            right: 0.14rem;
            }
        }
        
        .sbg{
            width: 100%;
            height: 0.56rem;
            background-color: rgba(7,17,27,0.5);
            position: absolute;
            top: 10;
            left: 0;
            right: 0;
            bottom: 0;
        }
        .smbg img{
                width: 0.47rem;
                height: 0.26rem;
                padding:2.27rem 0.08rem 0.04rem 0.24rem;
        }
        .smbg .word{
                padding:2.3rem 0.5rem 0.04rem 0.8rem;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
        }
        .kz{
            font-size: 0.26rem;
            position: absolute;
            top: 2.26rem;
            right: 0.24rem;
        }
        .clearfix{
            clear: both;
        }
    }
</style>




